.wrapper span {
    width: 100%;
    cursor: pointer;
}

.wrapper input {
    width: 100%;
    border: none;
    font-size: 17px;
    cursor: pointer;
    background-color: rgb(207, 207, 207);
}

.wrapper input:focus {
    outline: none;
    cursor: text;
    background-color: rgb(236, 236, 236);
}